import React from "react";
import "./CurrencyConverter.css";
import { Form, Input, Button, Select, DatePicker, Switch } from "antd";

const CurrencyConverter = () => {
  return (
    <>
      <Form
        labelCol={{
          span: 4,
        }}
        wrapperCol={{
          span: 4,
        }}
        layout="horizontal"
      >
        <Form.Item
          required
          label="Kwota"
          rules={[
            {
              required: true,
              message: "Please input your Username!",
            },
          ]}
        >
          <Input placeholder="Proszę podać kwotę do przeliczenia" />
        </Form.Item>
        <Form.Item label="Waluta" required>
          <Select placeholder="Na jaką walutę przeliczyć?">
            <Select.Option value="demo">Demo</Select.Option>
          </Select>
        </Form.Item>

        <Form.Item label="Data">
          <DatePicker initialValue="today" />
        </Form.Item>

        <Form.Item label="Switch">
          <Switch />
        </Form.Item>
        <Form.Item label="Kurs z ostatniego dnia roboczego">
          <Button type="primary" htmlType="submit">
            Przelicz
          </Button>
        </Form.Item>
      </Form>
    </>
  );
};

export default CurrencyConverter;
